// style.scss for Spin Defender Theme
// --------------------------------------------------
// Variables
$primary-green: #00ff5a;
$primary-black: #111;
$secondary-black: #181818;
$white: #fff;
$gray: #eee;
$shadow-green: 0 4px 24px #00ff5a22;
$shadow-green-strong: 0 8px 32px #00ff5a44;
$font-main: 'Inter', 'Helvetica Neue', Arial, sans-serif;

// Mixins
@mixin card {
  background: $primary-black;
  border-radius: 1.5rem;
  box-shadow: $shadow-green;
}
@mixin flex-center($gap: 2rem) { 
  display: flex;
  flex-wrap: wrap;
  gap: $gap;
  justify-content: center;
}

// Base
body {
  font-family: $font-main;
  background: $primary-black;
  color: $white;
  margin: 0;
  padding: 0;
}

// Header & Footer
.site-header, .site-footer {
  background: $secondary-black;
  color: $white;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  padding: 1.5rem 0;
}
.site-header .container, .site-footer .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  @include flex-center(1rem);
  align-items: center;
  justify-content: space-between;
}
.site-title a {
  color: $primary-green;
  text-decoration: none;
  font-weight: 700;
  font-size: 2rem;
  letter-spacing: -1px;
}
.site-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 2rem;
}
.site-nav a {
  color: $white;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
  &:hover {
    color: $primary-green;
  }
}

// Hero
.hero {
  background: #000;
  color: $white;
  text-align: center;
  padding: 5rem 2rem 3rem 2rem;
  position: relative;
  h1 {
    font-size: 3rem;
    font-weight: 900;
    letter-spacing: -2px;
    color: $primary-green;
    text-shadow: 0 2px 16px #000;
  }
  p {
    font-size: 1.5rem;
    color: $white;
    max-width: 600px;
    margin: 1.5rem auto 2rem auto;
  }
  .hero-image {
    max-width: 500px;
    width: 100%;
    margin: 2rem auto 0 auto;
    display: block;
    border-radius: 2rem;
    box-shadow: $shadow-green-strong;
  }
}

// CTA Button
.cta-btn {
  background: $primary-green;
  color: $primary-black;
  border: none;
  border-radius: 2rem;
  padding: 1rem 2.5rem;
  font-size: 1.25rem;
  font-weight: 700;
  cursor: pointer;
  box-shadow: 0 4px 16px #00ff5a33;
  margin-top: 1.5rem;
  display: inline-block;
  text-decoration: none;
  transition: background 0.2s, box-shadow 0.2s;
  &:hover {
    background: darken($primary-green, 10%);
    box-shadow: 0 8px 32px #00ff5a66;
  }
}

// Product Section
.product-section {
  max-width: 1100px;
  margin: 4rem auto;
  padding: 2rem;
  background: $secondary-black;
  border-radius: 2rem;
  box-shadow: $shadow-green;
  @include flex-center(3rem);
  align-items: center;
  .product-image {
    flex: 1 1 350px;
    min-width: 300px;
    max-width: 400px;
    border-radius: 1.5rem;
    box-shadow: $shadow-green-strong;
  }
  .product-info {
    flex: 2 1 400px;
    h2 {
      font-size: 2.25rem;
      font-weight: 800;
      color: $white;
      margin-bottom: 1rem;
    }
    p {
      font-size: 1.15rem;
      color: $gray;
      margin-bottom: 1.5rem;
    }
    ul {
      color: $primary-green;
      font-size: 1.1rem;
      margin-bottom: 1.5rem;
      padding-left: 1.2em;
    }
  }
}

// Testimonials
.testimonials {
  max-width: 1000px;
  margin: 3rem auto;
  h3 {
    color: $primary-green;
    text-align: center;
    font-size: 2rem;
    font-weight: 800;
    margin-bottom: 2rem;
  }
  .testimonial {
    @include card;
    padding: 2rem;
    flex: 1 1 300px;
    min-width: 260px;
    margin-bottom: 2rem;
    img {
      border-radius: 50%;
      margin-bottom: 1rem;
    }
    p {
      font-style: italic;
      color: $white;
    }
    span {
      color: $primary-green;
      font-weight: 700;
    }
  }
}

// CTA Section
.cta-section {
  background: #000;
  text-align: center;
  padding: 3rem 2rem;
  h2 {
    color: $primary-green;
    font-size: 2.5rem;
    font-weight: 900;
    margin-bottom: 1rem;
  }
  p {
    color: $white;
    font-size: 1.25rem;
    margin-bottom: 2rem;
  }
}

// Social Showcase
.social-showcase {
  background: $secondary-black;
  padding: 4rem 2rem;
  h2 {
    color: $primary-green;
    text-align: center;
    font-size: 2.25rem;
    font-weight: 900;
    margin-bottom: 2.5rem;
  }
}

/*
  IMPORTANT: Masonry.js requires .grid-item to use float: left for multi-column layout.
  Do NOT set display: flex or display: grid on .grid or any parent container.
  If you change SCSS, recompile to style.css and hard-refresh your browser.
*/
.grid {
  background: $secondary-black;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  /* Prevent accidental flex/grid on parent */
  display: block !important;
}
.grid:after {
  content: '';
  display: block;
  clear: both;
}
.grid-sizer,
.grid-item {
  width: 25%; // 4 columns on desktop
}
.grid-item {
  float: left !important; // Prevent override by any other rule
  margin-bottom: 32px;
  background: $primary-black;
  box-shadow: $shadow-green;
  border-radius: 1.5rem;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
  display: block;
}
.action-video,
.video-placeholder {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 1rem;
  background: $primary-black;
}
.play-pause-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0,0,0,0.6);
  border: none;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  z-index: 2;
  cursor: pointer;
  outline: none;
  opacity: 0;
  transition: opacity 0.2s;
  .play-icon {
    display: block;
    width: 0;
    height: 0;
    border-left: 24px solid $primary-green;
    border-top: 14px solid transparent;
    border-bottom: 14px solid transparent;
    margin-left: 10px;
  }
  .pause-icon {
    display: none;
    width: 24px;
    height: 28px;
    position: relative;
    span {
      display: inline-block;
      width: 8px;
      height: 28px;
      background: $primary-green;
      margin-right: 4px;
      &:last-child { margin-right: 0; }
    }
  }
}
.grid-item:hover .play-pause-btn,
.grid-item:focus-within .play-pause-btn {
  opacity: 1;
}

@media (max-width: 900px) {
  .grid-sizer,
  .grid-item {
    width: 50%; // 2 columns on tablet
  }
}
@media (max-width: 600px) {
  .grid-sizer,
  .grid-item {
    width: 100%; // 1 column on mobile
  }
}
